import {
	setToken,
	getToken,
	removeToken,
	setUserInfo,
	getUserInfo,
	removeUserInfo,
	setloginuserInfo,
	getloginuserInfo,
	removeloginuserInfo
} from "./auth";
import {
	baseUrl,
	uploadUrl
} from "./apiconfig";
import {
	log
} from "util";
// let baseUrl = 'https://ga.rasmall.cn/prod-api/wxapi/';
// let baseUrl = 'http://localhost:8083/wxapi/'

//token
let isRefreshing=false;
// token
let refreshSubscribers=[];
// token
function refreshToken(){
	if(isRefreshing){
		//tokenPromise
		return new Promise((resolve)=>{
			refreshSubscribers.push(resolve);
		});
	}
	isRefreshing=true;
	return new Promise((resolve,reject)=>{
		uni.request({
			url:`${baseUrl}/auth/refresh`,// tokenAPI
			method:'POST',
			header:{
				'Authorization':`Bearer ${uni.getStorageSync('refreshToken')}`,
			},
			success:(res)=>{
				if(res.data.code===200){
					const newToken=res.data.data.token;
					const newRefreshToken=res.data.data.refreshToken;
					
					//tokenrefreshToken
					uni.setStorageSync('token',newToken);
					uni.setStorageSync('refreshToken',newRefreshToken);
					//
					refreshSubscribers.forEach((callback)=>callback(newToken));
					refreshSubscribers=[];
					resolve(newToken);
				}else{
					reject('Refresh Token Failed');
				}
			},
			fail:(err)=>reject(err),
			complete() {
				isRefreshing=false;
			}
		});
	})
}
async function request(mehtod, params, type, customHeader = {}) {
	//request
	if (!type) {
		type = 'GET';
	}
	let tokenValue = getToken();
	const defaultHeader = {
		"Authorization": tokenValue ? 'Bearer ' + getToken() : '',
		// 'X-Requested-With': 'XMLHttpRequest',
		"Accept": "application/json",
		"Content-Type": "application/json; charset=UTF-8",
	};
	// header(headerheader)
	const header = {
		...defaultHeader,
		...customHeader
	};
	// Token
	// if (requireToken) {
	// 	const tokenValue = getToken();
	// 	if (tokenValue) {
	// 		header.Authorization = `Bearer ${tokenValue}`;
	// 	}
	// }
	const http = {
		url: baseUrl + mehtod,
		data: params,
		method: type,
		header: header
	};
	let promise = new Promise((resolve, reject) => {
		uni.request(http).then(res => {
			console.log("res",res);
			// let newdata = res[1].data; // if (newdata.code == 403) {
			// if (newdata.code !== 200) {
			// 	// -1 
			// 	uni.showToast({
			// 		title: res[1].data.msg,
			// 		icon: 'none'
			// 	});
			// }
			// resolve(res[1].data);
			// const {
			// 	statusCode,
			// 	data
			// } = res[1];
			// Token
			let newdata = res[1].data;
			if(newdata.code===401){
				uni.showToast({title:'',icon:'none'})
				removeToken();
				removeUserInfo();
				removeloginuserInfo();
				setTimeout(()=>{
					uni.redirectTo({
						url:'/pages/login/login'
					})
				},1000)
				return reject(res[1].data)
			}
			// console.log("statusCode",statusCode,data);
			if(newdata.code===200){
				resolve(res[1].data);
			}else{
				// console.log("500",res[1].data.msg)
				uni.showToast({title:res[1].data.msg || '',icon:'none'});
				reject(res[1].data)
			}
		}).catch((err)=>{
			uni.showToast({title:'',icon:'none'})
			reject(err)
		})
	});
	return promise;
}

async function requestJson(mehtod, params, type, headerParams, callBack) {
	//request
	let header = {
		//
		'X-Requested-With': 'XMLHttpRequest',
		"Accept": "application/json",
		"Content-Type": "application/json; charset=UTF-8"
	};
	if (headerParams) {
		header = Object.assign(header, headerParams);
	}
	let http = {
		url: mehtod,
		data: params,
		method: type,
		header: header
	};
	let promise = new Promise((resolve, reject) => {
		uni.request(http).then(res => {
			resolve(res);
		}).catch(err => {
			reject(err);
			console.log(err);
		});
	});
	return promise;
}

async function requestForm(mehtod, params, type, callBack) {
	//request
	let header = {
		//
		'X-Requested-With': 'XMLHttpRequest',
		"Accept": "application/json",
		"Content-Type": "application/x-www-form-urlencoded"
	};
	let http = {
		url: mehtod,
		data: params,
		method: type,
		header: header
	};
	let promise = new Promise((resolve, reject) => {
		uni.request(http).then(res => {
			resolve(res);
		}).catch(err => {
			reject(err);
			console.log(err);
		});
	});
	return promise;
}
export default {
	request,
	requestJson,
	requestForm
};